/* DarkModeChat.css */

.App {
  display: flex;
  justify-content: center;
  align-items: center;
}
body {
  margin: 0;

  background-color: #182418;
}
/* In your CSS file */
.navbar {
  background-color: #333;
  color: #fff;
  padding: 10px;
}

.navbar-brand {
  font-size: 1.5em;
  font-weight: bold;
}

.chat-container {
  background-color: #f0e9e9;
  width: 700px;
  height: 650px;
  margin-top: 20px;
  border: 1px solid #555;
  border-radius: 8px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.chat-history {
  flex-grow: 1;
  overflow-y: auto;
  padding: 10px;
  color: #182418;
  /* padding: 10px 15px 10px 0; */
}
.chat-history::-webkit-scrollbar {
  width: 3px; /* Width of the scrollbar */
  margin-right: 2px;
}

.chat-history::-webkit-scrollbar-thumb {
  background-color: #ed7777; /* Color of the scrollbar thumb */
  border-radius: 3px; /* Radius of the scrollbar thumb */
}

.chat-history::-webkit-scrollbar-track {
  background-color: #f4f4f4; /* Color of the scrollbar track */
}

.message {
  margin-bottom: 10px;
  padding: 8px 16px;
  border-radius: 20px;
  max-width: 70%;
}

.user {
  align-self: flex-start;

  color: black;
  margin-bottom: 5px;
  font-weight: bold;
  text-transform: capitalize;
}

.bot {
  align-self: flex-end;

  color: rgb(0, 128, 107);
  font-size: 15px;
  margin-bottom: 10px;
  font-weight: bold;
  background-color: #e0bcbc81;
  padding: 15px;
  border-radius: 10px;
}

.input-container {
  display: flex;
  align-items: center;
  padding: 10px;
  border-top: 1px solid #555;
}

.input {
  flex: 1;
  padding: 8px;
  border: none;

  border-radius: 20px;
  margin-right: 10px;
}

form input {
  width: 500px;
  padding: 7px 10px;
  margin-bottom: 20px;
  margin-right: 10px;
}
.ui1 {
  margin-left: 10px;
  font-size: 25px;
}
form button {
  padding: 8px 16px;
  border: none;
  border-radius: 20px;

  cursor: pointer;
}
form button:hover {
  background-color: #555;
  color: #f4f4f4;
}
@media screen and (max-width: 768px) {
  .chat-container {
    width: 90%;
    height: 87vh;
    margin-top: 10px;
  }

  .message {
    max-width: 90%;
  }

  /* .input {
    width: ;
  } */

  form input {
    width: 50%;
    border-radius: 3px;
    border: none;
  }
  form button {
    background-color: #264226;
    color: wheat;
  }
}
